import service from "../../service.js";
export default{
	data(){
		return{
			img_arr:'',
			id:'',
			content:'',
			subBoolean:true
		}
	},
	methods:{
		jump(url){
			uni.navigateTo({
				url
			});
		},
		close(){
			this.img_arr = '';
		},
		upload(){
			var self = this;
			uni.chooseImage({
			    success: (chooseImageRes) => {
			        const tempFilePaths = chooseImageRes.tempFilePaths;
			        uni.uploadFile({
			            url: service.api.user.updateIdImage, //仅为示例，非真实的接口地址
						header: {
							Authorization: 'Bearer ' + uni.getStorageSync('token')
						},
			            filePath: tempFilePaths[0],
			            name: 'file',
			            success: (res) => {
			                console.log(JSON.parse(res.data),"asfasdf");
							const path = JSON.parse(res.data);//打印出来就知道为什么要用JSON.parse
							// self.invoiceimg = path.data.url;
							// console.log(path)
							self.img_arr = path.data.url
							console.log(self.img_arr)
							uni.showToast({
								title:path.info,
								icon:'none'
							});
			            }
			        });
			    }
			});
		},
		Submi(){
			
			if(this.img_arr == ''){
				uni.showToast({
					icon:'none',
					title:'请上传凭证！'
				})
				return
			}
			if(this.content == ''){
				uni.showToast({
					icon:'none',
					title:'输入内容！'
				})
				return
			}
			if(this.subBoolean){
				service.auth(this , service.api.service.submitWorkorder , 'POST' , {
				screenshot:this.img_arr,
				content:this.content
			} , function(self ,res){
				self.subBoolean = false;
				console.log(res);
				uni.showToast({
					icon:'none',
					title:res.info
				});
					setTimeout(function(){
						uni.navigateTo({
							url:'/pages/service/feedback'
						})
					},1500);
			});
			}
		},
	}
}
